

import { Injectable } from '@angular/core';
import { BehaviorSubject, Subject } from 'rxjs';
import { TDSSafeAny } from 'tds-ui/shared/utility'

@Injectable()
export class TDSMenuService {
  mode$ = new BehaviorSubject<'dark' | 'light'>('dark');
  isChildSubMenuOpen$ = new BehaviorSubject<boolean>(false);
  /** all descendant menu click **/
  descendantMenuItemClick$ = new Subject<any>();
  onDescendantMenuItemClick(menu: any): void {
    this.descendantMenuItemClick$.next(menu);
  }
  groupMenuOpen$ = new BehaviorSubject<TDSSafeAny>(null);
  onModeChange(mode: 'dark' | 'light') {
    this.mode$.next(mode);
  }
}
