import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ContentChild, ContentChildren, EventEmitter, Input, OnChanges, OnDestroy, OnInit, Optional, Output, QueryList, SimpleChanges, SkipSelf, ViewChildren, ViewEncapsulation } from '@angular/core';
import { BehaviorSubject, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { InputBoolean, TDSHelperArray, TDSHelperString, TDSSafeAny } from 'tds-ui/shared/utility';
import { TDSMenuOptionDTO } from '../dto/tds-menu-option.dto';
import { TDSMenuDTO } from '../dto/tds-menu.dto';
import { TDSMenuService } from '../services/menu.service';
import { TDSMenuGroupInlineComponent } from '../tds-menu-group-inline/tds-menu-group-inline.component';
import { TDSMenuGroupPopupComponent } from '../tds-menu-group-popup/tds-menu-group-popup.component';
export function TDSMenuServiceFactory(serviceInsideDropDown: TDSMenuService, serviceOutsideDropDown: TDSMenuService): TDSMenuService {
  return serviceInsideDropDown ? serviceInsideDropDown : serviceOutsideDropDown;
}

@Component({
  selector: 'tds-menu',
  templateUrl: './tds-menu.component.html',
  styleUrls: ['./tds-menu.component.css'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  host: {
    class: "h-full flex flex-col box-border",
    '[class.w-full]': `!inlineCollapsed`,
    '[class.w-16]': `inlineCollapsed`,
  },
  providers: [
    /** use the top level service **/
    TDSMenuService
  ]
})
export class TDSMenuComponent implements OnInit, OnChanges, OnDestroy {
  private destroy$ = new Subject<void>();

  listMenu: Array<TDSMenuDTO> = [];
  @Input() @InputBoolean() showLogo = false;
  @Input() data: Array<any> = [];
  @Input() mode: 'dark' | 'light' = 'dark';
  @Input() showIcon: boolean = true;
  @Input() @InputBoolean() inlineCollapsed = false;
  @Input() options: TDSMenuOptionDTO = {
    background: 'bg-white dark:bg-d-neutral-3-200',
    backgroundItem: 'bg-white dark:bg-d-neutral-3-200',
    backgroundItemSelected: 'bg-neutral-3-50 dark:bg-d-neutral-3-300',
    backgroundItemHover:'dark:hover:bg-d-neutral-3-300  hover:bg-neutral-3-50'
  }
  @Output() readonly onClickItem = new EventEmitter<TDSMenuDTO>();
  @Output() readonly onOpenChange = new EventEmitter<boolean>();
  @ViewChildren(TDSMenuGroupInlineComponent) listOfTDSMenuGroupInlineComponent!: QueryList<TDSMenuGroupInlineComponent>
  @ViewChildren(TDSMenuGroupPopupComponent) listOfTDSMenuGroupPopupComponent!: QueryList<TDSMenuGroupPopupComponent>
  @ContentChild('logo-text') logoText: any;
  private inlineCollapsed$ = new BehaviorSubject<boolean>(this.inlineCollapsed);
  constructor(private _cdr: ChangeDetectorRef,
    private tdsMenuService: TDSMenuService,) {

  }

  ngOnInit(): void {

    this.tdsMenuService.descendantMenuItemClick$.pipe(takeUntil(this.destroy$)).subscribe(menu => {
      this.onClickItem.emit(menu.item);
      if (TDSHelperArray.hasListValue(this.listOfTDSMenuGroupInlineComponent)) {
        this.listOfTDSMenuGroupInlineComponent.forEach(group => {
          group.setSelectedStateListChildren(menu.uid);
        }
        );
      }
      if (TDSHelperArray.hasListValue(this.listOfTDSMenuGroupPopupComponent)) {
        this.listOfTDSMenuGroupPopupComponent.forEach(group => {
          group.setSelectedStateListChildren(menu.uid);
        }
        );
      }

    });
    this.tdsMenuService.groupMenuOpen$.pipe(takeUntil(this.destroy$)).subscribe(menu => {
      if (TDSHelperArray.hasListValue(this.listOfTDSMenuGroupInlineComponent)) {
        this.listOfTDSMenuGroupInlineComponent.forEach(group => {
          if (group.hasListChild)
            group.setOpenStateListChildren(group == menu && !group.item.isOpen);
        });
      }
    });
  }
  ngOnChanges(changes: SimpleChanges) {
    const { data, inlineCollapsed, mode } = changes;
    if (data) {
      this.p_mapData(this.data);
    }
    if (inlineCollapsed) {
      this.inlineCollapsed$.next(this.inlineCollapsed)
    }
    if (mode) {
      this.tdsMenuService.onModeChange(this.mode);
    }
    this._cdr.markForCheck();
  }
  private p_mapData(data: Array<any>) {
    if (!TDSHelperArray.hasListValue(data)) {
      this.listMenu = [];
    } else {
      this.listMenu = data.map(item => {
        return this.p_mapItem(item);
      })
    }
  }
  private p_mapItem(data: any): TDSMenuDTO {
    let listChild: Array<TDSSafeAny> = []

    if (TDSHelperArray.hasListValue(data.listChild)) {
      listChild = data.listChild.map((item: TDSSafeAny) => {
        return this.p_mapItem(item)
      })
    }
    return {
      name: data.name,
      icon: data.icon,
      disabled: data.disabled,
      isSelected: data.isSelected,
      isOpen: data.isOpen,
      link: data.link,
      listChild: listChild,
      uid: TDSHelperString.genid(),
      hidden: data.hidden,
      groupTitle: data.groupTitle,
      htmlIcon:data.htmlIcon,
      linkProps:data.linkProps,
      badge:data.badge,
      tag:data.tag,
      dot:data.dot,
    }
  }
  ngAfterViewInit(): void {
    this.inlineCollapsed$.pipe(takeUntil(this.destroy$)).subscribe(() => {
      this.updateInlineCollapse();
      this._cdr.markForCheck();
    });
  }
  updateInlineCollapse(): void {
    if (this.inlineCollapsed) {
      if (this.listOfTDSMenuGroupInlineComponent.length) {
        this.listOfTDSMenuGroupInlineComponent.forEach(group => {
          group.setOpenStateListChildren(false);
        })
      }
    } else {
      if (this.listOfTDSMenuGroupPopupComponent.length) {
        this.listOfTDSMenuGroupPopupComponent.forEach(group => {
          group.setOpenStateListChildren(false);
        })
      }
    }
  }
  setInlineCollapsed(inlineCollapsed: boolean): void {
    this.inlineCollapsed = inlineCollapsed;
    this.inlineCollapsed$.next(inlineCollapsed);
  }
  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
  onClickInlineCollapsed() {
    this.inlineCollapsed = !this.inlineCollapsed;
    this.onOpenChange.emit(this.inlineCollapsed);
    this.inlineCollapsed$.next(this.inlineCollapsed);
  }
  get hasIcon() {
    if (!this.showIcon) {
      return false;
    }
    const lstIcon = this.listMenu.filter(f => TDSHelperString.hasValueString(f.icon));
    return lstIcon.length > 0
  }
}
